import React from 'react';
import { Helmet } from 'react-helmet'
import Navbar from './Navbar';
import Botbar from './Botbar';
import './Demo.css'

const TITLE = "Demo mode"

function Demo() {
    return (
        <div className="demo">
            <Helmet><title>{TITLE}</title></Helmet>
            <div className="hero_wrapper_demo">
                <Navbar />
                <div className="demo_text">
                    <p>We run the site in demo mode, because our twitter API has a limit on how many tweets we can scrape. If our site would function normally every visit would trigger a new scrape of the most recent trends, which would download thousands of tweets per trend. That means that in a short amount of time we would reach out limit, set by Twitter. 
Therefore we decided, that we would scrape trends when something interesting is happening and use this data to feed our algorithm. </p>
                </div>
            </div>
            <Botbar />
        </div>
    );
}

export default Demo;