import React from 'react';
import { Helmet } from 'react-helmet'
import Navbar from './Navbar';
import Botbar from './Botbar';
import './AboutProject.css'
import headertext from '../images/headertext.png'
import pipeline from '../images/pipeline.png'

const TITLE = "About the Project"

function AboutProject() {
    return (
        <div className="aboutproject">
            <Helmet><title>{TITLE}</title></Helmet>
            <div className="hero_wrapper_about">
                <Navbar />
                <div className="project">
                    <img className="heading" src={headertext} alt="Our Project" />
                    <p>Twitter trends are increasingly becoming people’s first choice when they want to inform themselves about whats happening in the world. Millions of messages are appearing every day about people’s lives, opinions on a variety of topics and latest discussions.</p>
                </div>
                <div className="ap_motivation">
                    <h4>Our Motivation</h4>
                    <p>Even for twitter users that spend multiple hours per day on twitter, it can be hard to get a solid overview and background information about the latest twitter trends. This can be considered as a big data challenge, and it is difficult and time consuming for users to go through enough tweets for every trend to be able to make a good decision which trend is of relevance. In this research, we developed a model that provides decision guidance in terms of the relevance
                    of the twitter top 50 trend using textual features. The results of this project can help twitter
                    users to get faster to the trends they are appealed to most, and to get a first insight to all trends and
their resonance in the community at a glance.</p>
                </div>
                <div className="ap_topic_words">
                    <h4>Feature: Topic Words</h4>
                    <p>To get a quick overview about the different themes in a trend, the Topic Words feature uses topic extraction to show you what words are most commonly used in that specific tweet. This feature helps you to stay up to date with all the discussion happening around a topic.</p>
                </div>
                <div className="ap_wordcloud">
                    <h4>Feature: Wordcloud</h4>
                    <p>In a world of colourful pictures a visualisation of the data is not allowed to be missing. This feature shows you the most used words by creating a word cloud out of them.</p>
                </div>

                <div className="ap_sentiment">
                    <h4>Feature: Trend Sentiment</h4>
                    <p>Our Sentiment Analysis provides a quick overview of what emotions are subjectively perceived about a trend in percentages via a pie chart. It is based on a ruled-based system that operates on a predefined lexicon.</p>
                </div>

                <div className="ap_top_links">
                    <h4>Feature: Top Links</h4>
                    <p>This feature extracts the most called links of a specific trend and lists them, providing a useful summary for further research.</p>
                </div>

                <div className="pipeline">

                    <h4>Feature: Pipeline</h4>
                    <div className="pipeline_content">
                        <div className="pipeline_content_top">
                            <img className="pipeline_graph" src={pipeline} alt="Visualization of Pipeline" />
                            <div>
                                <p>The pipeline processes the data in the following steps:</p>
                                <p>1. The get_links_from_tweet function searches for all links in a tweet and counts them in a dict.</p>
                                <p>2. The remove_numbers_and_links function deletes all numbers, links and removes underscores, because we encountered a problem where they are present in the word cloud.</p>
                            </div>
                        </div>
                        <p>3. In the function clean_tweets all non-alphabetic characters are removed and all is set in lower case.</p>
                        <p>4. Here it removes all stop words. We added a additional stop words, because twitter users use specific twitter abbreviations that do not offer any value for us.</p>
                        <p>5. After removing the stop words some tokens where left with one char or even empty, so it removes them in this step.</p>
                        <p>6. In this step it removes the empty lists that remained after the removal of one char and empty tokens.</p>
                        <p>7. Finally it adds all words in a dict and counts them.</p>
                    </div>
                </div>
            </div>
            <Botbar />
        </div>
    );
}

export default AboutProject;